<template>
  <div id="app">
    <div id="top" :style="getBackgroundColor">
      <GithubCorner :discordcolor="colors.discordfill" :discordfill="colors.discordcolor"/>
      <DiscordCorner customLink="#" @click.native="setPreviewType('corner')" id="discordcorner" :discordcolor="colors.discordfill" :discordfill="colors.discordcolor"/>
      <ColorSelector customLink="#" @colorChange="onColorChange"/>
      <DiscordSwirl customLink="#" @click.native="setPreviewType('standard')" :discordfill="colors.discordfill" :discordcolor="colors.discordcolor"/>
      <br/>
      <DiscordText customLink="#" ref="discordtext" :standardText="standardText" @click.native="updateSpeechBubble() + setPreviewType('speechbubble')" :discordfill="colors.discordfill" :discordcolor="colors.discordcolor" />
    </div>
    <MainBody ref="mainbody" :previewDiscordType="previewDiscordType"/>
  </div>
</template>

<script>
import DiscordSwirl from './components/DiscordSwirl.vue'
import GithubCorner from './components/GithubCorner.vue'
import DiscordCorner from './components/DiscordCorner.vue'
import ColorSelector from './components/ColorSelector.vue'
import MainBody from './components/MainBody.vue'
import DiscordText from './components/DiscordText.vue'

export default {
  name: 'app',
  components: {
    DiscordSwirl, GithubCorner, DiscordCorner, ColorSelector, MainBody, DiscordText
  },
  data () {
  		return {
        // isTyping: false,
        standardText: 'Join us on Discord',
        previewDiscordType: 'standard',
        colors: {
          discordcolor: '#FFFFFF',
          discordfill: '#7289DA'
        }
  		}
  	},
  methods: {
    updateSpeechBubble: function () {
      var myArray = ['Click me to get the speech bubble code generator!',
                    'Click top right GitHub corner to fork!',
                    'Animation or style ideas? Raise an issue on GitHub!',
                    'Created with  by NNTin.',
                    'Click on the Discord corner to get the code.',
                    "All discord logos are clickable and will not redirect you!"];
      var rand = myArray[Math.floor(Math.random() * myArray.length)];
      this.standardText = rand;
    },
    onColorChange (value) {
      this.colors = value
      this.$refs.mainbody.changeColor(this.colors.discordcolor, this.colors.discordfill)
    },
    setPreviewType (value) {
      console.log(value)
      this.previewDiscordType = value;
    }
  },
  computed: {
    getBackgroundColor: {
			get: function () {
				return {
					backgroundColor: this.colors.discordfill
				}
			}
		}
  },
  created: function() {
    this.updateSpeechBubble()
  }
}
</script>

<style>
html,body {
  height: 100%;
  margin: 0px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
}
#discordcorner {
	position: absolute;
	top: 0;
	left: 0;
	border: 0;
}
</style>



